import Axios from 'axios';
import { SearchType, ListCars } from '../../../../shared/store.interfaces';
import { defaultUrls } from '../../../../shared/config';
import { getDOMBetweenBody, removeNotDigits } from '../../../../shared/function-helpers';
import { environment } from 'src/environments/environment';

export const tipCars = async (
  listNumberTipCars: number,
  search: SearchType,
  ip: string,
): Promise<ListCars[]> => {
  try {
    const res = await Axios.post(`${defaultUrls.local}api-type`, {
      listNumberSAuto: listNumberTipCars,
      ip,
      search: JSON.stringify(search),
      type: 'tipCars',
    });
    if (!!res && 'data' in res) {
      function arrayFrom(el: HTMLCollectionOf<Element> | NodeListOf<Element>): Array<Element> {
        return Array.from(el);
      }
      const div = document.createElement('div');
      div.innerHTML = getDOMBetweenBody(res.data);
      const inzeratyDOM = div.getElementsByClassName('inzerat_v_browsu');
      const nadpisyDOM = div.querySelectorAll('.w-100 h2');
      const priceDOM = div.getElementsByClassName('koncova_cena');
      const imagesBoxDOM = div.querySelectorAll('.inzerat_v_browsu .mt-10 .foto');
      const createdByDOM = div.querySelectorAll('a.w-100');
      const mileageDOM = div.querySelectorAll('.trojudaj .najeto');
      const yearsDOM = div.querySelectorAll('.trojudaj .rok_vyroby');
      if (!inzeratyDOM || !nadpisyDOM) {
        return [];
      }
      const nadpisy = arrayFrom(nadpisyDOM);
      const images = arrayFrom(imagesBoxDOM);
      const prices = arrayFrom(priceDOM);
      const createdBys = arrayFrom(createdByDOM);
      const mileages = arrayFrom(mileageDOM);
      const years = arrayFrom(yearsDOM);
      const tmpCarList: ListCars[] = [];
      for (let x = 0; x < arrayFrom(inzeratyDOM).length; x++) {
        tmpCarList.push({
          name: nadpisy[x].innerHTML || '',
          type: 'tipCars',
          description: '',
          image: !!images && !!images[x] && images[x].hasAttribute('src') ? images[x].getAttribute('src') : '',
          price: removeNotDigits(prices[x].innerHTML),
          createdBy: createdBys[x].hasAttribute('href') ? `${defaultUrls.tipCarsMini}${createdBys[x].getAttribute('href')}` : '',
          icon: '/assets/images/tipcars.png',
          city: '',
          mileage: mileages[x].innerHTML && mileages[x].innerHTML.length > 2  ? `${removeNotDigits(mileages[x].innerHTML)}.000` : '',
          yearMade: years[x].innerHTML && mileages[x].innerHTML.length > 2 ? years[x].innerHTML : '',
        })
      }
      return tmpCarList;
    }
  } catch (err) {
    console.log('tipCars catch 1 :>> ', err);
    return [];
  }
};
