import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { t } from 'shared/language/language';
import { languageConfig } from 'shared/config';

@Component({
  selector: 'app-erace-button',
  template: `
    <mat-icon
      aria-hidden="false"
      class="cancel_button"
      [ngClass]="{'card_active': condition}"
      [title]="t.placeHoldersAndLabels.labelCancelYearFrom"
      (click)="removeFromSearch()"
    >
      clear
    </mat-icon>
  `,
  styleUrls: ['./erace-button.component.scss']
})
export class EraceButtonComponent implements OnInit {
  @Input() condition: boolean;
  @Input() id: string;
  @Output() removeFunc = new EventEmitter<string>();

  t = t[languageConfig];

  constructor() { }

  ngOnInit(): void {
  }

  removeFromSearch() {
    this.removeFunc.emit(this.id);
  }
}
