// import * as cheerio from 'cheerio';
import Axios from 'axios';
import { getDOMBetweenBody, changeRowsNumDueToPriceFrom } from '../../../../shared/function-helpers';
import { defaultUrls } from '../../../../shared/config';
import { ListCars, SearchType } from '../../../../shared/store.interfaces';

const domain = defaultUrls.bazosMainUrl;

export type BazosType = {list: ListCars[], numNew: number};

export const bazos = async (
  listNumberSAuto: number,
  search: SearchType,
  ip: string,
  sortOrder: number = 1,
): Promise<BazosType> => {
  let res: {list: ListCars[], numNew: number} = {list: [], numNew: listNumberSAuto};
  if (!!search.kmFrom || !!search.kmTo || !!search.yearFrom || !!search.yearTo || !!search.fuel) {
    const rowsKmFromOrKmTo = changeRowsNumDueToPriceFrom(
      search.priceFrom || search.priceTo,
      !!search.yearFrom || !!search.yearTo,
      !!search.fuel
    );
    const promises: Array<Promise<BazosType>> = [];
    for (let x = 0; x <= rowsKmFromOrKmTo; x++) {
      promises.push(declareListOfItems(res.numNew, search, ip, sortOrder));
      if (x < rowsKmFromOrKmTo) {
        res.numNew = res.numNew + 2;
      }
    }
    await Promise.all(promises)
    .then(response => {
      for (const resList of response) {
        res.list = [...res.list, ...resList.list];
      }
    })
    .catch(err => {
      console.log('err All catch :>> ', err);
      return {list: [], numNew: listNumberSAuto};
    });
  } else {
    res = await declareListOfItems(listNumberSAuto, search, ip, sortOrder);
  }
  return res;
};

const declareListOfItems = async (
  listNumberSAuto: number,
  search: SearchType,
  ip: string,
  sortOrder: number = 1,
): Promise<BazosType> => {
  try {
    const res = await Axios.post(`${defaultUrls.local}api-type`, {
      listNumberSAuto,
      ip,
      sortOrder,
      search: JSON.stringify(search),
      type: 'bazos',
    });
    if (!!res && 'data' in res) {
      const tmpRes: ListCars[] = [];
      const div = document.createElement('div');
      div.innerHTML = getDOMBetweenBody(res.data);
      const nadpisy = div.getElementsByClassName('nadpis');
      const ceny = div.getElementsByClassName('cena');
      const inzeraty = div.getElementsByClassName('inzeraty');
      const popis = div.getElementsByClassName('popis');
      if (
        !!nadpisy && !!ceny && !!inzeraty
      ) {
        const arrCeny = Array.from(ceny).map(cena => {
          return !!(cena as HTMLElement).innerText.length
          ? (cena as HTMLElement).innerText === 'Zdarma'
            ? (cena as HTMLElement).innerText
            : ((cena as HTMLElement).innerText).replace(/[^0-9]+/gi, '')
          : ''
        });
        const responseArrayList = await Axios.post(`${defaultUrls.local}get-html-bazos`, {
          nadpisy: arrFromInnerText(nadpisy),
          ceny: arrCeny,
          popis: arrFromInnerText(popis),
          images: Array.from(inzeraty).map(inzerat => {
            return inzerat.children[0].children[0].children[0].children[3];
          }).map(photo => {
            return photo.localName === 'a' ? photo.children[0].attributes['src'].nodeValue || '' : '';
          }),
          createdBys: Array.from(nadpisy).map(nadpis => {
            return (nadpis.childNodes[0] as HTMLElement).attributes['href'].nodeValue;
          }),
          cities: Array.from(inzeraty).map(inzerat => {
            return inzerat.children[0].children[0].children[2].childNodes[0].nodeValue
          }),
          search,
        });
        if (!!responseArrayList && 'data' in responseArrayList) {
          tmpRes.push(...responseArrayList.data);
        } else {
          console.log('err responseArrayList');
          return {list: tmpRes, numNew: listNumberSAuto};
        }
      }
      return {list: tmpRes, numNew: listNumberSAuto};
    } else {
      console.log('err bazos api-type');
      return {list: [], numNew: listNumberSAuto};
    }
  } catch (error) {
    console.log('err bazos catch :>> ', error);
    return {list: [], numNew: listNumberSAuto};
  }
}

function arrFromInnerText(items: HTMLCollectionOf<Element>) {
  return Array.from(items).map(item => (item as HTMLElement).innerText);
}
