import { Component, OnInit, OnDestroy } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { Observable, Subscription, Subject } from 'rxjs';
import { ListState } from 'src/store/list/list.effect';
import { ListCars, SearchType } from '../../../shared/store.interfaces';
import { FormControl, FormGroup } from '@angular/forms';
import { AddListItem, DeleteListItem, LoadingContent } from 'src/store/list/list.actions';
import { bazos, BazosType } from './search-web/bazos';
import { sAuto } from './search-web/sAuto';
import { aaaauto, AaaautoType } from './search-web/aaaauto';
import { searchItemDefaultValues } from 'src/store/list/list.state';
import { configuration, languageConfig, defaultUrls } from '../../../shared/config';
import { searKeywordMatchExcept, splitInDots, createMileage, createLink, resolveOrStop, removeFromTheListOfCompanies } from '../../../shared/function-helpers';
import { t } from '../../../shared/language/language';
import { Companies, companies, companyWarning } from '../../../shared/companies';
import { customCardDefault } from '../../../shared/utils';
import { tipCars } from './search-web/tipCars';
import { sbazar } from './search-web/sbazar';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-search-result',
  templateUrl: './search-result.component.html',
  styleUrls: ['./search-result.component.scss'],
})
export class SearchResultComponent implements OnInit, OnDestroy {
  @Select(ListState.SelectAllItems) listItemsSubscribe: Observable<ListCars[]>;
  @Select(ListState.SearchItemsValue) searchItemSubscribe: Observable<SearchType>;
  @Select(ListState.SelectorListOfCompanies) listOfCompaniesSubscribe: Observable<Companies>;
  @Select(ListState.SelectorDetectedIp) ipObservable: Observable<string>;

  readonly companyWarning = companyWarning;
  t = t[languageConfig];
  form: any;
  search: SearchType = searchItemDefaultValues;
  showItemAdded = false;
  listOfCompanies = companies;
  JSON: any;
  ip = '';
  itemsLoaded = [];
  itemsNotLoaded = Object.keys(companies).filter(c => companies[c]);

  listNumberBazos = 0;
  listNumberSauto = 1;
  listNumberAaaAuto = 1;
  listNumberTipCars = 1;
  listNumberSbazar = 1;

  loadingContent = false;
  tmpBazosList: BazosType[] = [];
  tmpBazosCountList: ListCars[] = [];
  isAaaautoHasPagenav = true;
  listItems: ListCars[] = [];
  private destroy: Subject<boolean> = new Subject<boolean>();
  currentKey = Math.random();
  customCard = customCardDefault;

  constructor(private store: Store) {
    this.JSON = JSON;
    this.form = new FormGroup({
      listItem: new FormControl('')
    });
  }

  ngOnInit() {
    this.listItemsSubscribe.pipe(takeUntil(this.destroy)).subscribe(res => {
      if (!!res) {
        this.listItems = res;
      }
    });
    this.ipObservable.pipe(takeUntil(this.destroy)).subscribe(res => {
      if (!!res) {
        this.ip = res;
      }
    });
    this.searchItemSubscribe.pipe(takeUntil(this.destroy)).subscribe(res => {
      if (!!res) {
        this.search = res;
        this.makeDefaultValues(true);
        this.preHandlerSearch(false);
      }
    });
    this.listOfCompaniesSubscribe.pipe(takeUntil(this.destroy)).subscribe(res => {
      this.itemsNotLoaded = Object.keys(companies).filter(c => companies[c]);
      if (!!res) {
        console.log(res);
        const tmpListOfCompanies = this.listOfCompanies;
        this.listOfCompanies = {...res};
        if (this.listItems && !!this.listItems.length) {
          const items = this.listItems.filter(list => {
            return this.listOfCompanies[list.type];
          });
          for (let x = 0; x < Object.keys(res).length; x++) {
            if (
              Object.values(tmpListOfCompanies)[x] !== Object.values(res)[x]
              && !!Object.values(res)[x]
            ) {
              this.makeDefaultValues(false);
              this.preHandlerSearch(false);
            } else {
              if (this.loadingContent) {
                return;
              }
              this.store.dispatch(new DeleteListItem());
              this.listItems = [];
              this.submitItem(items, undefined, undefined, true);
            }
          }
        } else {
          this.makeDefaultValues(true);
          this.preHandlerSearch(false);
        }
      }
    });
    this.preHandlerSearch(false);
  }

  makeDefaultValues(bool?: boolean) {
    if (!!bool) {
      this.tmpBazosCountList = [];
      this.listItems = [];
    }
    this.listNumberBazos = 0;
    this.listNumberSauto = 1;
    this.listNumberAaaAuto = 1;
    this.listNumberTipCars = 1;
    this.listNumberSbazar = 0;
    this.isAaaautoHasPagenav = true;
  }

  increaseAllValues() {
    this.listNumberSauto = this.listNumberSauto + 1;
    this.listNumberBazos = this.listNumberBazos + 2;
    this.listNumberAaaAuto = this.listNumberAaaAuto + 1;
    this.listNumberTipCars = this.listNumberTipCars + 1;
    this.listNumberSbazar = this.listNumberSbazar + 1;
  }

  createPrice(price: string) {
    if (!price) {
      return price;
    }
    if (!!price.match(/(.*)zdarma(.*)/gi)) {
      return price;
    }
    return `${splitInDots(price)} ${this.t.header.currentCurrency}`;
  }

  onScroll(e) {
    if (!!this.loadingContent) {
      return;
    }
    if ((document.body.scrollHeight - window.innerHeight - window.scrollY) < 50) {
      if (!this.loadingContent) {
        this.preHandlerSearch(true);
      }
    }
  }

  addAndRemoveFromCompanies(company: string) {
    if (!this.itemsLoaded.includes(company)) {
      this.itemsLoaded.push(company);
      this.itemsNotLoaded = removeFromTheListOfCompanies(company, this.itemsNotLoaded);
      console.log(`${company} loaded`);
    }
  }

  preHandlerSearch(isScrolling: boolean) {
    this.loadingContent = true;
    this.store.dispatch(new LoadingContent(true));
    this.currentKey = Math.random();
    if (!isScrolling) {
      this.store.dispatch(new DeleteListItem());
    }
    const arrPromisesBefore: Array<Promise<ListCars[] | BazosType | AaaautoType>> = [];
    
    // bazos
    if (this.listOfCompanies.bazoš && configuration.isBazosSearchOn) {
      if (this.tmpBazosCountList.length <= 19) {
        arrPromisesBefore.push(
          this.assignBazos()
          .then(res => {
            this.addAndRemoveFromCompanies(Object.keys(companies)[0])
            return res;
          })
          .catch(err => {console.log('err bazos :>> ', err); return []})
        );
      } else {
        arrPromisesBefore.push(
          new Promise(resolve => resolve())
          .then(() => {
            console.log('bazos loaded tmp');
            return this.tmpBazosCountList;
          })
          .catch(err => {console.log('err bazos tmp :>> ', err); return []})
        )
      }
    }

    // sAuto
    if (this.listOfCompanies.sauto && configuration.isSautoSearchOn) {
      arrPromisesBefore.push(
        this.assignSauto()
        .then(res => {
          this.addAndRemoveFromCompanies(Object.keys(companies)[1])
          return res;
        })
        .catch(err => {console.log('err sauto :>> ', err); return []})
      );
    }

    // aaaAuto
    if (
      this.listOfCompanies.aaaauto
      && configuration.isAaaautoSearchOn
      && this.isAaaautoHasPagenav
    ) {
      arrPromisesBefore.push(
        this.assignAaaauto()
        .then(res => {
          this.addAndRemoveFromCompanies(Object.keys(companies)[2])
          return res;
        })
        .catch(err => {console.log('err aaaauto :>> ', err); return []})
      );
    }

    // tipCars
    if (this.listOfCompanies.tipCars && configuration.isTipCasSearchOn) {
      arrPromisesBefore.push(
        this.assignTipCar()
        .then(res => {
          this.addAndRemoveFromCompanies(Object.keys(companies)[3])
          return res;
        })
        .catch(err => {console.log('err tipCars :>> ', err); return []})
      );
    }

    // sbazar
    if (this.listOfCompanies.sbazar && configuration.isSbazarSearchOn) {
      arrPromisesBefore.push(
        this.assignSbazar()
        .then(res => {
          this.addAndRemoveFromCompanies(Object.keys(companies)[0])
          return res;
        })
        .catch(err => {console.log('err sbazar :>> ', err); return []})
      );
    }

    Promise.all(arrPromisesBefore)
    .then((res) => {
      this.itemsLoaded = [];
      this.itemsNotLoaded = Object.keys(companies).filter(c => companies[c]);
      const tmpArr: ListCars[] = [];
      let tmpNum = 0;
      let aaaautoHasPagenav = true;
      for (const lists of res) {
        if (!!lists) {
          const onlyLists = 'list' in lists ? lists.list : lists;
          const searchExcept = searKeywordMatchExcept(this.search.keyword);
          const nadpisWithExceptation = !searchExcept || !searchExcept.length
          ? onlyLists
          : Array.from(onlyLists).filter(nadpis => {
              for (const expres of searchExcept) {
                return !!nadpis && !(nadpis.name.toLowerCase())
                  .includes(expres.toLowerCase());
              }
            });
          tmpArr.push(...(nadpisWithExceptation || []));
          if ('list' in lists && 'numNew' in lists) {
            tmpNum = lists.numNew;
          }
          if ('list' in lists && 'aaaautoHasPagenav' in lists) {
            aaaautoHasPagenav = lists.aaaautoHasPagenav;
          }
        }
      }
      // TODO: did not found reason why this is working without this
      // if (!aaaautoHasPagenav) {
      //   console.log('tmpArr :>> ', tmpArr);
      //   this.submitItem(tmpArr, undefined, aaaautoHasPagenav);
      // }
      if (!!res && res.length) {
        this.submitItem(tmpArr, tmpNum);
      }
      this.loadingContent = false;
      this.store.dispatch(new LoadingContent(false));
    })
    .catch((err) => console.log('err Promise all search :>> ', err));
  }

  async assignSauto() {
    try {
      return await resolveOrStop(sAuto(this.listNumberSauto, this.search, this.ip));
    } catch (err) {
      console.log('err sAuto complet :>> ', err);
      this.loadingContent = false;
      return [];
    }
  }

  async assignAaaauto() {
    try {
      return await resolveOrStop(aaaauto(this.listNumberAaaAuto, this.search, this.ip));
    } catch (err) {
      console.log('err aaaAuto complet :>> ', err);
      this.loadingContent = false;
      return [];
    }
  }

  async assignBazos() {
    try {
      return await resolveOrStop(bazos(this.listNumberBazos, this.search, this.ip));
    } catch (err) {
      console.log('err bazos complet :>> ', err);
      this.loadingContent = false;
      return [];
    }
  }

  async assignTipCar() {
    try {
      return await resolveOrStop(tipCars(this.listNumberSauto, this.search, this.ip));
    } catch (err) {
      console.log('err tipCars complet :>> ', err);
      this.loadingContent = false;
      return [];
    }
  }

  async assignSbazar() {
    try {
      return await resolveOrStop(sbazar(this.listNumberSauto, this.search, this.ip));
    } catch (err) {
      console.log('err sbazar complet :>> ', err);
      this.loadingContent = false;
      return []
    }
  }

  submitItem = (
    items: ListCars[],
    bazosNum?: number,
    aaaautoHasPagenav?: boolean,
    doNotIncreaseCurrentNumbers?: boolean,
  ) => {
    // const sortedArray = this.shuffleArrays(items);
    if (aaaautoHasPagenav === false) {
      this.isAaaautoHasPagenav = aaaautoHasPagenav;
    }
    this.form.reset();
    const filterIncommingArraySelf = items
      .filter((item, index, self) =>
        index === self.findIndex((t) => (
          t.createdBy === item.createdBy
        ))
        || !!item.isItCardDevider
      )
      .filter((item) =>
        !!item.isItCardDevider
        || !this.listItems.map(l => l.createdBy).includes(item.createdBy)
      );
    let filterIncommingArraySelfBazos: ListCars[] = filterIncommingArraySelf
      .filter(item => item.type === 'bazoš');
    let notFilterIncommingArraySelfBazos: ListCars[] = filterIncommingArraySelf
      .filter(item => item.type !== 'bazoš');
    filterIncommingArraySelfBazos = this.filterLengthBazos(filterIncommingArraySelfBazos);
    const allItems = [...filterIncommingArraySelfBazos, ...notFilterIncommingArraySelfBazos];
    this.store.dispatch(new AddListItem(allItems || []));
    if (doNotIncreaseCurrentNumbers) {
      return;
    }
    if (!!bazosNum) {
      this.listNumberBazos = bazosNum;
    }
    this.increaseAllValues();
  }

  filterLengthBazos(tmpArr: ListCars[]) {
    this.tmpBazosCountList = this.tmpBazosCountList.filter(auto => (
      tmpArr.map(tmp => tmp.createdBy).includes(auto.createdBy)
    ));
    if (this.tmpBazosCountList.length) {
      tmpArr = [...this.tmpBazosCountList, ...tmpArr];
    }
    if (tmpArr.length <= 20) {
      this.tmpBazosCountList = [];
      return tmpArr;
    }
    this.tmpBazosCountList = tmpArr.splice(20, tmpArr.length - 20);
    return tmpArr;
  }

  shuffleArrays(array: ListCars[]) {
    const shuffled = array.sort(() => Math.random() - 0.5);
    return shuffled.sort((a, b) => parseFloat(a.price) - parseFloat(b.price));
  }

  sortArray(array: ListCars[]) {
    return array.sort((a, b) => parseFloat(a.price) - parseFloat(b.price));
  }

  createMileage = (mileage: string) => createMileage(mileage);

  checkIfItCardIsDevider(isItCardDevider: ListCars['isItCardDevider']) {
    return !!isItCardDevider;
  }

  deviderTitle(i: number) {
    return typeof this.listItems[i + 1] !== 'undefined' ? this.listItems[i + 1].type : '';
  }

  createLink = (i: number) => createLink(i, this.listItems);

  toolTipClick(e: MouseEvent) {
    e.preventDefault();
    e.stopPropagation();
    return false;
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}
