import { State } from '@ngxs/store';
import { ListCars, SearchType, SearchModelsInterface } from '../../../shared/store.interfaces';
import { Injectable } from '@angular/core';
import { companies, Companies } from '../../../shared/companies';
import { SearchEnums } from '../../../shared/utils';

export interface ListStateModel {
  list?: ListCars[];
  searchItem?: SearchType;
  searchModels?: SearchModelsInterface[];
  listOfCompanies?: Companies;
  detecedIp?: string;
  detectChange?: number,
  loadingContent?: boolean,
}

export const searchModelsDefaultValues = [
  {
    n: '',
    v: 0,
  }
];

export const searchItemDefaultValues: SearchType = {
  keyword: '',
  priceFrom: '',
  priceTo: '',
  kmFrom: '',
  kmTo: '',
  brand: searchModelsDefaultValues[0],
  model: searchModelsDefaultValues[0],
  fuel: '',
  yearFrom: '',
  yearTo: '',
  regionId: SearchEnums.default,
};

@Injectable()
@State<ListStateModel>({
  name: 'ListState',
  defaults: {
    list: [],
    searchItem: searchItemDefaultValues,
    searchModels: searchModelsDefaultValues,
    listOfCompanies: companies,
    detecedIp: '',
    detectChange: Math.random(),
    loadingContent: false,
  }
})

export class ListState {}
