import { Component, Inject, Input, EventEmitter, Output } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ListCars } from '../../../../shared/store.interfaces';
import { languageConfig, defaultUrls } from '../../../../shared/config';
import { t } from '../../../../shared/language/language';
import { Subscription, Subject } from 'rxjs';
import { EmailBodyType } from '../../../../shared/global-interfaces';
import { HttpClient } from '@angular/common/http';
import { Email } from '../../../../shared/utils';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-send-email-dialog',
  templateUrl: './send-email-dialog.component.html',
  styleUrls: ['./send-email-dialog.component.scss']
})
export class SendEmailDialogComponent {
  @Input() data: string;
  @Output() erraceData = new EventEmitter<[]>();
  t = t[languageConfig];
  emailTo = '';
  private destroy: Subject<boolean> = new Subject<boolean>();
  emailToWarning: string;
  succesSend = false;
  errorSend = false;
  emailLoading = false;

  constructor(
    // public dialogRef: MatDialogRef<SendEmailDialogComponent>,
    // @Inject(MAT_DIALOG_DATA) public data: ListCars[],
    private http: HttpClient,
  ) {}

  sendEmail() {
    if (!this.emailTo) {
      this.emailToWarning = this.t.lovedList.sendEmail.emailToWarning;
      return;
    }
    if (!this.validateEmail) {
      this.emailToWarning = this.t.lovedList.sendEmail.notCorrectFormatOfEmail;
      return;
    }
    this.makeItValuesDefault();
    const body: EmailBodyType = {
      emailTo: this.emailTo,
      data: JSON.stringify(this.data),
    };
    this.http.post(`${defaultUrls.local}send-email`, body, {responseType: 'text'})
    .pipe(takeUntil(this.destroy))
    .subscribe((res: string) => {
      this.emailLoading = false;
      if (res === Email.SUCCESS) {
        this.succesSend = true;
        setTimeout(() => this.erraceData.emit([]), 1500);
      } else {
        this.errorSend = true;
      }
    });
  }

  makeItValuesDefault() {
    this.emailLoading = true;
    this.succesSend = false;
    this.errorSend = false;
  }

  emailChanged() {
    this.emailToWarning = '';
    this.errorSend = false;
  }

  onNoClick(event?: MouseEvent): void {
    if (!!event && event.target !== event.currentTarget) {
      return;
    }
    this.erraceData.emit([]);
  }

  get validateEmail() {
    const re = /\S+@\S+\.\S+/;
    return re.test(this.emailTo);
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}

