import { Component, OnInit, OnDestroy } from '@angular/core';
import { t } from '../../../shared/language/language';
import { languageConfig, defaultUrls } from '../../../shared/config';
import { Store, Select } from '@ngxs/store';
import { SearchItem, SearchModels, DeleteModels } from 'src/store/list/list.actions';
import { SearchType, SearchModelsInterface } from '../../../shared/store.interfaces';
import { brands } from '../../../shared/config-folder/brands';
import { HttpClient } from '@angular/common/http';
import { Subscription, Observable, Subject } from 'rxjs';
import { Router } from '@angular/router';
import { disabled, fuel, year } from 'src/store/enums';
import { searchItemDefaultValues } from 'src/store/list/list.state';
import { ListState } from 'src/store/list/list.effect';
import { defaultModels, customBrands, fuelTypes, Constants } from '../../../shared/utils';
import { searchModelsDefaultValues } from '../../store/list/list.state';
import { splitInDots } from '../../../shared/function-helpers';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, OnDestroy {
  @Select(ListState.SelectorModels) allModels: Observable<SearchModelsInterface[]>;

  t = t[languageConfig];
  search: SearchType = searchItemDefaultValues;
  disabledCar = disabled.CAR;
  disabledFuelOption = fuel.CAR;
  disabledYear = {
    yearFrom: year.YEAR_FROM,
    yearTo: year.YEAR_TO,
  };
  fuelValues = [];
  arrYears = Array.from({length: 120}, (k,x) => new Date().getFullYear() - x);
  yearsFrom = [this.disabledYear.yearFrom, ...this.arrYears];
  yearsTo = [this.disabledYear.yearTo, ...this.arrYears];
  brandsTypes: string[] = [
    this.t.header.chooseBrand,
    ...customBrands.map(brand => {
      return brands.find(auto => brand === auto.n)?.n;
    }),
    disabled.CAR.toString(),
    ...brands.map(brand => brand.n),
  ];
  modelTypes: Array<SearchModelsInterface> = [defaultModels];
  mapToggle = false;
  buttonClicked = false;
  mapHeight = 0;
  confirmDialog = false;
  private destroy: Subject<boolean> = new Subject<boolean>();

  constructor(private store: Store, private http: HttpClient) {
    this.fuelValues = [this.disabledFuelOption, ...fuelTypes.map(f => f.name)];
  }

  ngOnInit(): void {
    this.allModels.pipe(takeUntil(this.destroy)).subscribe(res => {
      if (!!res) {
        this.modelTypes = res;
      }
    });
  }

  focusTo(focusTo: string) {
    if (focusTo === 'fuel') {
      return (document.querySelector(`#${focusTo}`) as HTMLElement).click();
    }
    (document.querySelector(`#${focusTo}`) as HTMLElement).focus();
  }

  getModels(urlX: string) {
    if (!urlX) {
      return;
    }
    this.store.dispatch(new DeleteModels());
    this.modelTypes = [defaultModels];
    const brand = brands.find(b => b.n === urlX);
    this.search.brand = brand;
    this.search.model = searchModelsDefaultValues[0];
    // this.makeSearch();
    this.searchModels(brand);
  }

  selectModel(modelString: string) {
    const model = this.modelTypes.find(b => b.n === modelString);
    this.search.model = model;
    // this.makeSearch();
  }

  selectFuel(fuelType: string) {
    this.search.fuel = fuelType;
    // this.makeSearch();
  }

  selectYearFrom(yearFrom: string) {
    this.search.yearFrom = yearFrom;
    // this.makeSearch();
  }

  selectYearTo(yearTo: string) {
    this.search.yearTo = yearTo;
    // this.makeSearch();
  }

  searchRegion(regionId: string) {
    this.search.regionId = regionId;
    this.makeSearch();
  }

  makeSearch() {
    console.log('this.search :>> ', this.search);
    window.scroll(0, 0);
    if (
      this.search.keyword.length
      || this.search.priceFrom.length
      || this.search.priceTo.length
      || this.search.kmFrom.length
      || this.search.kmTo.length
      || !!this.search.brand
      || !!this.search.model
      || this.search.fuel.length
      || this.search.yearFrom.length
      || this.search.yearTo.length
      || this.search.regionId.length
    ) {
      this.store.dispatch(new SearchItem({
        ...this.search,
        kmFrom: this.search.kmFrom.length ? this.search.kmFrom.replace(/(\.|\s)/g, '') : '',
        kmTo: this.search.kmTo.length ? this.search.kmTo.replace(/(\.|\s)/g, '') : '',
        priceFrom: this.search.priceFrom.length ? this.search.priceFrom.replace(/(\.|\s)/g, '') : '',
        priceTo: this.search.priceTo.length ? this.search.priceTo.replace(/(\.|\s)/g, '') : '',
      }));
    }
  }

  searchModels(brand: SearchModelsInterface) {

    // Sauto search model
    const brandReplaced = brand.n.replace(/\s/gi, '_');
    const urlSauto = `${defaultUrls.local}sautoModels?brand=${brandReplaced}`;
    this.http.get(urlSauto).pipe(takeUntil(this.destroy))
    .subscribe((res: string) => {
      if (!res || !!res && !res.length) {
        this.modelTypes = [{
          n: this.disabledCar.toString(),
          v: -1,
          a: -1
        }];
      }
      const modelsSautoTmp: SearchModelsInterface[] = [{
        n: this.t.header.chooseModel,
        v: -1,
        a: -1,
      }];
      for (const b of JSON.parse(res)) {
        // b = {name: string; val: number}
        modelsSautoTmp.push({
          n: b.name,
          v: b.val,
        });
      }
      this.store.dispatch(new SearchModels(modelsSautoTmp));
    });
  }

  keyUp(searchTarget: string, value: string, isNumber?: boolean) {
    let newValue = !value ? '' : value;
    if (isNumber) {
      newValue = newValue.replace(/[^0-9]/gi, '');
    }
    this.search[searchTarget] = !newValue ? '' : splitInDots(newValue);
    (document.getElementById(searchTarget) as HTMLInputElement).value = this.search[searchTarget];
  }

  getMapHeight(num: number) {
    if (window.innerWidth >= Constants.customWidth) {
      (document.getElementById('position_devider') as HTMLElement).style.marginBottom = `${num}px`;
      this.mapHeight = num;
    }
  }

  scrollToTop() {
    window.scrollTo(0, 0);
  }

  makeToggleMap(e: {buttonClicked: boolean, toggleMap: boolean}) {
    this.buttonClicked = e.buttonClicked;
    this.mapToggle = e.toggleMap;
    setTimeout(() => this.buttonClicked = false, 500);
  }

  removeFromSearch(type: keyof SearchType | '') {
    if (!type) {
      return;
    }
    const elem = (document.getElementById(type) as HTMLElement);
    if (!!elem && 'selectedIndex' in elem) {
      (elem as HTMLSelectElement).selectedIndex = 0;
      if (type === 'brand') {
        (document.getElementById('model') as HTMLSelectElement).selectedIndex = 0;
        // @ts-ignore
        this.search['model'] = '';
      }
    }
    // @ts-ignore
    this.search[type] = '';
  }

  clearAll() {
    document.querySelectorAll('select').forEach(select => select.selectedIndex = 0);
    Object.keys(this.search).forEach(key => {
      this.search[key] = '';
    })
  }

  get selectModelIsDisabled() {
    return this.modelTypes[0].n === this.t.header.chooseModel && this.modelTypes.length === 1;
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}
