import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ListCars } from '../../../../shared/store.interfaces';
import { createPrice, isExistsInLovedList, setToLovedList, removeFromLovedList, createMileage } from '../../../../shared/function-helpers';
import { t } from '../../../../shared/language/language';
import { languageConfig } from '../../../../shared/config';
import { companyWarning } from '../../../../shared/companies';
import { Store } from '@ngxs/store';
import { DetectChangeInStorage } from 'src/store/list/list.actions';


@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {
  @Input() auto: ListCars;

  t = t[languageConfig];
  readonly companyWarning = companyWarning;

  constructor(private store: Store) { }

  ngOnInit(): void {
  }

  createPrice = (price: string) => createPrice(price);

  createMileage = (mileage: string) => createMileage(mileage);

  toolTipClick(e: MouseEvent) {
    e.preventDefault();
    e.stopPropagation();
    return false;
  }

  hearthClick(e: MouseEvent, auto: ListCars) {
    e.preventDefault();
    e.stopPropagation();
    this.store.dispatch(new DetectChangeInStorage());
    if (!isExistsInLovedList(auto)) {
      setToLovedList(auto);
    } else {
      removeFromLovedList(auto);
    }
    return false;
  }

  simulateHref(link: string) {
    window.open(link, '_blank');
  }

  isInLovedList(auto: ListCars) {
    return isExistsInLovedList(auto);
  }
}
