import { Component, OnInit, OnDestroy } from '@angular/core';
import { ListCars } from '../../../shared/store.interfaces';
import { getLovedList, removeAllFromLoveList } from '../../../shared/function-helpers';
import { t } from '../../../shared/language/language';
import { languageConfig, defaultUrls } from '../../../shared/config';
import { Select, Store } from '@ngxs/store';
import { Observable, Subscription, Subject } from 'rxjs';
import { ListState } from 'src/store/list/list.effect';
import { DetectChangeInStorage } from 'src/store/list/list.actions';
import { MatDialog } from '@angular/material/dialog';
import { SendEmailDialogComponent } from './send-email-dialog/send-email-dialog.component';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-love-list',
  templateUrl: './love-list.component.html',
  styleUrls: ['./love-list.component.scss']
})
export class LoveListComponent implements OnInit, OnDestroy {
  @Select(ListState.SelectorDetectChange) detectChange: Observable<number>;

  t = t[languageConfig];
  loveIsOpen = false;
  listLovedCars: ListCars[] = [];
  dialogData: ListCars[] = [];
  private destroy: Subject<boolean> = new Subject<boolean>();

  constructor(private store: Store, public dialog: MatDialog) {}

  ngOnInit(): void {
    this.detectChange.pipe(takeUntil(this.destroy)).subscribe(res => {
      this.getLoveListe();
    });
  }

  closeFromBg(event) {
    if (!!event && event.target !== event.currentTarget) {
      return;
    }
    this.loveIsOpen = !this.loveIsOpen;
  }

  getLoveListe() {
    setTimeout(() => this.listLovedCars = getLovedList(), 100);
  }

  emptyLocal() {
    removeAllFromLoveList();
    this.store.dispatch(new DetectChangeInStorage());
    this.loveIsOpen = !this.loveIsOpen;
  }

  sendEmail() {
    const localStorage = getLovedList();
    if (!localStorage.length) {
      return alert(this.t.lovedList.noPossibleSendEmptyEmail)
    }
    this.dialogData = localStorage;
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}
