import Axios from 'axios';
import { SearchType, ListCars } from '../../../../shared/store.interfaces';
import { defaultUrls, languageConfig } from '../../../../shared/config';
import { ResultSbazar } from '../../../../shared/utils';
import { t as lang} from '../../../../shared/language/language';
import { findMileageInText, findYearMade } from 'shared/function-helpers';
import { environment } from 'src/environments/environment';

export const sbazar = async (
  listNumberSbazar: number,
  search: SearchType,
  ip: string,
): Promise<ListCars[]> => {
  let t = lang[languageConfig];
  try {
    const res = await Axios.post(`${defaultUrls.local}api-type`, {
      listNumberSAuto: listNumberSbazar,
      ip,
      search: JSON.stringify(search),
      type: 'sbazar',
    });
    if (!!res && 'data' in res) {
      const data: ResultSbazar[] = res.data.results;
      if (!data.length) {
        return [];
      }
      const tmpCarsList: ListCars[] = [];
      function createPrice(auto: ResultSbazar) {
        if (auto.price_by_agreement) {
          return t.body.agrement;
        }
        if (!auto.price || auto.price === 0) {
          return t.body.free;
        }
        return String(auto.price);
      }
      for (const auto of data) {
        tmpCarsList.push({
          name: auto.name,
          city: auto.locality.municipality ? auto.locality.municipality : auto.locality.district,
          type: 'sbazar',
          description: '',
          image: auto.images.length ? `https:${auto.images[0].url}?fl=exf|res,280,280,3|jpg,80,,1`: '',
          price: createPrice(auto),
          createdBy: `https://www.sbazar.cz/${auto.user.user_service.shop_url}/detail/${auto.seo_name}`,  // https://www.sbazar.cz/LiLoCars/detail/18033123-airbag-a-bezpecnostni-pasy
          icon: '/assets/images/sauto.png',
          mileage: findMileageInText(auto.name, ''),
          yearMade: findYearMade(auto.name, ''),
        });
      }
      return tmpCarsList;
    }
  } catch (err) {
    console.log('sbazar catch :>> ', err);
    return [];
  }
};
