import { ListStateModel } from './list.state';
import { Selector, Action, StateContext } from '@ngxs/store';
import { ListCars, SearchType, SearchModelsInterface } from '../../../shared/store.interfaces';
import { AddListItem, DeleteListItem, SearchItem, SearchModels, DeleteModels, ChangeListOfCompanies, DetectChangeInStorage, DetectIp, LoadingContent } from './list.actions';
import { CompaniesType, Companies } from '../../../shared/companies';
import { cardDevider } from 'shared/utils';

export class ListState {
  @Selector() static SelectAllItems(state: ListStateModel): ListCars[] {
    return state.list;
  }

  @Selector() static SearchItemsValue(state: ListStateModel): SearchType {
    return state.searchItem;
  }

  @Selector() static SelectorModels(state: ListStateModel): SearchModelsInterface[] {
    return state.searchModels;
  }

  @Selector() static SelectorListOfCompanies(state: ListStateModel): Companies {
    return state.listOfCompanies;
  }

  @Selector() static SelectorDetectChange(state: ListStateModel): number {
    return state.detectChange;
  }

  @Selector() static SelectorDetectedIp(state: ListStateModel): string {
    return state.detecedIp;
  }

  @Selector() static SelectorLoadingContent(state: ListStateModel): boolean {
    return state.loadingContent;
  }

  @Action(AddListItem)
  addListItem(
    { getState, setState }: StateContext<ListStateModel>,
    { payload }: AddListItem
  ) {
    const state = getState();
    const allListCars = [...(state?.list || []), ...payload];
    const tmpAutoList: ListCars[] = [];
    for (let x = 0; x < allListCars.length; x++) {
      const isDevider = allListCars[x].isItCardDevider;
      if (x === 0 && !isDevider) {
        tmpAutoList.push(cardDevider);
      } else if (typeof allListCars[x - 1] === 'undefined') {
        true;
      } else if (
        allListCars[x].type !== allListCars[x - 1].type
        && allListCars[x - 1].type !== null
        && allListCars[x].type !== null
      ) {
        tmpAutoList.push(cardDevider);
      }
      tmpAutoList.push(allListCars[x]);
    }
    if (allListCars.length !== tmpAutoList.length) {
      tmpAutoList.push(cardDevider);
    }
    setState({
      list: tmpAutoList,
    });
  }

  @Action(DeleteListItem)
  deleteListItem(
    { getState, setState }: StateContext<ListStateModel>,
  ) {
    const state = getState();
    setState({
      ...state,
      list: []
    });
  }

  @Action(SearchItem)
  searchItem(
    { getState, setState }: StateContext<ListStateModel>,
    { payload }: SearchItem
  ) {
    const state = getState();
    setState({
      ...state,
      searchItem: {...payload},
    });
  }

  @Action(SearchModels)
  searchModelEffect(
    { getState, setState }: StateContext<ListStateModel>,
    { payload }: SearchModels
  ) {
    const state = getState();
    setState({
      ...state,
      searchModels: [...payload],
    });
  }

  @Action(DeleteModels)
  deleteModels(
    { getState, setState }: StateContext<ListStateModel>,
  ) {
    const state = getState();
    setState({
      ...state,
      searchModels: []
    });
  }

  @Action(ChangeListOfCompanies)
  changeListOfCompaniesEffect(
    { getState, setState }: StateContext<ListStateModel>,
    { payload }: {payload: Companies}
  ) {
    const state = getState();
    setState({
      ...state,
      listOfCompanies: {...payload},
    });
  }

  @Action(DetectIp)
  detectIpEffect(
    { getState, setState }: StateContext<ListStateModel>,
    { payload }: { payload: string },
  ) {
    const state = getState();
    setState({
      ...state,
      detecedIp: payload,
    });
  }

  @Action(DetectChangeInStorage)
  detectChangesFromLoveList(
    { getState, setState }: StateContext<ListStateModel>,
  ) {
    const state = getState();
    setState({
      ...state,
      detectChange: Math.random(),
    });
  }

  @Action(LoadingContent)
  LoadingContentEffect(
    { getState, setState }: StateContext<ListStateModel>,
    { payload }: { payload: boolean },
  ) {
    const state = getState();
    setState({
      ...state,
      loadingContent: payload,
    });
  }
}
