import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { t } from 'shared/language/language';
import { languageConfig } from 'shared/config';
import { FormBuilder } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

type DaysChoosen = 'day' | 'days_little' | 'days_full';

@Component({
  selector: 'app-advertisement',
  templateUrl: './advertisement.component.html',
  styleUrls: ['./advertisement.component.scss']
})
export class AdvertisementComponent implements OnInit, OnDestroy {
  t = t[languageConfig];
  private destroy: Subject<boolean> = new Subject<boolean>();
  activeDay: DaysChoosen = 'day';

  constructor(private router: Router, public fb: FormBuilder) { }

  ngOnInit(): void {
    this.registrationForm.valueChanges.pipe(takeUntil(this.destroy)).subscribe((res: {days_choosen: DaysChoosen}) => {
      if (!!res) {
        this.activeDay = res.days_choosen;
      }
    });
  }

  navigateToRoot() {
    this.router.navigate(['/']);
  }

  registrationForm = this.fb.group({
    days_choosen: ['day'],
  })

  onSubmit() {
    console.log('object');
    return;
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}
