import { Component, OnInit, HostListener } from '@angular/core';
import { bannersConfig } from '../../../../shared/banners-config';
import { Constants } from '../../../../shared/utils';
import { bannerAreOn } from '../../../../shared/config';

type TypeOfScreen = 'big' | 'small';
@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss']
})
export class BannerComponent implements OnInit {
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (this.screenWidth !== this.typeOfScreen) {
      this.typeOfScreen = this.screenWidth;
      this.createImage();
    }
  }
  bannerAreOn = bannerAreOn;

  bannersConfig = bannersConfig;
  typeOfScreen: TypeOfScreen = 'big';
  image = '';
  imageUrl = '';
  isBigFormatBanner: boolean;

  constructor() {}

  createImage() {
    const defaultImage = bannersConfig
      [this.typeOfScreen]
      [Math.floor(Math.random() * bannersConfig[this.typeOfScreen].length)]
    this.image = defaultImage.img;
    this.imageUrl = defaultImage.url;
  }

  ngOnInit(): void {
   this.typeOfScreen = this.screenWidth;
   this.createImage();
  }

  get screenWidth(): TypeOfScreen {
    let is: TypeOfScreen = 'big';
    is = Constants.customWidth < window.innerWidth ? 'big' : 'small';
    return is;
  }
}
