import { SearchType, ListCars } from '../../../../shared/store.interfaces';
import Axios from 'axios';
import { defaultUrls } from '../../../../shared/config';
import { getDOMBetweenBody } from '../../../../shared/function-helpers';


export type AaaautoType = {list: ListCars[], aaaautoHasPagenav: boolean}

export const aaaauto = async (
  listNumberSAuto: number,
  search: SearchType,
  ip: string,
): Promise<{list: ListCars[]; aaaautoHasPagenav: boolean}> => {
  try {
    const res = await Axios.post(`${defaultUrls.local}api-type`, {
      listNumberSAuto,
      search: JSON.stringify(search),
      type: 'aaaauto',
      ip,
    });
    if (!!res && 'data' in res) {
      const tmpRes: ListCars[] = [];
      const div = document.createElement('div');
      div.innerHTML = getDOMBetweenBody(res.data);
      const notFound = div.querySelector('.notfound-wrapper h3');
      if (!!notFound) {
        return {list: [], aaaautoHasPagenav: false};
      }
      const nadpisyDom = div.getElementsByClassName('carFeatures');
      const pricesDom = div.getElementsByClassName('carFeaturesList');
      const carPriceDom = div.getElementsByClassName('carPriceSliderItem');
      const imagesDom = div.getElementsByClassName('carInfo');
      const pageNavDom = div.getElementsByClassName('pagenav');
      if (!!nadpisyDom) {
        const nadpisy = Array.from(nadpisyDom).map(nadpis => {
          const parentElement = (nadpis as HTMLElement)?.children[0]?.children[0];
          const mainNadpis = parentElement?.childNodes[0]?.nodeValue || '';
          let secondaryNadpis = '';
          if (!!parentElement?.childNodes[1]) {
            secondaryNadpis = parentElement?.childNodes[1]?.childNodes[0]?.nodeValue || '';
          }
          return {
            complateNadpis: `${mainNadpis}${secondaryNadpis}`,
            onlyYear: secondaryNadpis,
          };
        });
        const descriptions = Array.from(nadpisyDom).map((des) => {
          return (des as HTMLElement)?.children[1]?.childNodes[0]?.nodeValue || '';
        });
        const mileages = Array.from(pricesDom).map((mileage) => {
          const mileageText = (mileage as HTMLElement)?.children[0].childNodes[1]?.nodeValue;
          if (!!mileageText) {
            return mileageText.replace(/[^0-9]+/gi, '');
          }
          return '';
        });
        const createdBys = Array.from(nadpisyDom).map((create) => {
          return (create as HTMLElement)?.children[0]?.children[0]?.attributes['href']?.nodeValue || '';
        });
        const prices = Array.from(carPriceDom).map((price) => {
          let priceTmp = '';
          for (const priceElementFor of Array.from(price.childNodes)) {
            const priceElement = priceElementFor as HTMLElement;
            if (
              priceElement?.classList
              && (
                !priceElement.classList.length
                || (priceElement.classList.contains('hiden') && !priceElement.classList.contains('primary'))
              )
              && priceElement.tagName === 'H3'
            ) {
              priceTmp = priceElement.innerHTML.replace(/<.*?>.*?<\/.*?>/gi, '').replace(/[^0-9]+/gi, '');
            }
          }
          return priceTmp;
        }).filter(price => price);
        const images = Array.from(imagesDom).map((image) => {
          return (image as HTMLElement)?.children[0]?.children[2]?.children[0]?.attributes['src']?.nodeValue || '';
        });
        for (let x = 0; x < nadpisy.length; x++) {
          tmpRes.push({
            name: nadpisy[x].complateNadpis,
            city: '',
            description: descriptions[x],
            mileage: mileages[x],
            createdBy: createdBys[x],
            yearMade: nadpisy[x].onlyYear,
            type: 'aaaauto',
            price: prices[x],
            image: images[x],
            icon: '/assets/images/aaaauto.png',
          })
        }
        const pagenav = !!pageNavDom
          ? Array.from(pageNavDom).filter(pagenavEl => {
              return pagenavEl?.children[0].children.length > 3
            }).length !== 0
          : false;
        return {list: tmpRes, aaaautoHasPagenav: !!pagenav};
      }
      return {list: [], aaaautoHasPagenav: false};
    }
    return {list: [], aaaautoHasPagenav: false};
  } catch (error) {
    console.log('err aaaAuto main :>> ', error);
    return {list: [], aaaautoHasPagenav: false};
  }
};
