import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { companies } from '../../../../shared/companies';
import { Store, Select } from '@ngxs/store';
import { ChangeListOfCompanies } from 'src/store/list/list.actions';
import { t } from '../../../../shared/language/language';
import { languageConfig } from '../../../../shared/config';
import { regionsData } from '../../../../shared/utils';
import { ListState } from 'src/store/list/list.effect';
import { Observable, Subscription, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-under-header',
  templateUrl: './under-header.component.html',
  styleUrls: ['./under-header.component.scss']
})
export class UnderHeaderComponent implements OnInit, OnDestroy {
  @Select(ListState.SelectorLoadingContent) loadingSelector: Observable<boolean>;

  @Input() mapToggle: boolean;
  @Input() region: string;
  @Output() makeMapToggle = new EventEmitter<{buttonClicked: boolean, toggleMap: boolean}>();
  t = t[languageConfig];
  isLoading = false;
  listOfCompanies = companies;
  keysListOfCompanies = Object.keys(companies);
  private destroy: Subject<boolean> = new Subject<boolean>();

  constructor(private store: Store) { }

  ngOnInit(): void {
    this.loadingSelector.pipe(takeUntil(this.destroy)).subscribe(res => this.isLoading = res);
  }

  findRegion() {
    if (!this.region) {
      return '';
    }
    const name = regionsData.find(region => region.id === this.region)?.name;
    if (!!name) {
      return `: ${this.t.regions[name]}`;
    }
  }

  changeListOfCompanies(key, e) {
    const tmpListCompanies = this.listOfCompanies;
    tmpListCompanies[key] = e.checked;
    this.store.dispatch(new ChangeListOfCompanies(tmpListCompanies));
  }

  toggleMap() {
    this.makeMapToggle.emit({buttonClicked: true, toggleMap: !this.mapToggle});
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}
