import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription, Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { defaultUrls } from 'shared/config';
import { Store } from '@ngxs/store';
import { DetectIp } from 'src/store/list/list.actions';
import { environment } from 'src/environments/environment';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'autofay';

  private destroy: Subject<boolean> = new Subject<boolean>();

  constructor(private http: HttpClient, private store: Store) {}

  ngOnInit() {
    this.http.get(
      environment.production
        ? defaultUrls.getIpAsDataIp
        : `${defaultUrls.corsAnywhere}${defaultUrls.getIpAsDataIp}`
    ).pipe(takeUntil(this.destroy)).subscribe((res: { ip: string }) => {
      if (res) {
        this.store.dispatch(new DetectIp(res.ip));
      }
    })
  }

  ngOnDestroy() {
    this.destroy.next(true);
    this.destroy.unsubscribe();
  }
}
