import { SearchType, ListCars } from '../../../../shared/store.interfaces';
import Axios from 'axios';
import { defaultUrls } from '../../../../shared/config';
import { environment } from 'src/environments/environment';

export const sAuto = async (
  listNumberSAuto: number,
  search: SearchType,
  ip: string,
): Promise<ListCars[]> => {
  try {
    const result = await Axios.post(`${defaultUrls.local}api-type`, {
      listNumberSAuto,
      ip,
      search: JSON.stringify(search),
      type: 'sauto',
    });
    if (!!result && 'data' in result) {
      return result.data;
    }
  } catch (err) {
    console.log('sAuto catch :>> ', err);
    return [];
  }
};
