import { Component, Input, Output, EventEmitter, ViewChild, ElementRef, AfterViewInit, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { t } from '../../../../shared/language/language';
import { languageConfig } from '../../../../shared/config';
import { SearchEnums } from '../../../../shared/utils';

const colors = {
  white: 'white',
  black: 'black',
  hoverColor: 'grey',
  selectedColor: '#673ab7',
};

@Component({
  selector: 'app-mapa',
  templateUrl: './mapa.component.html',
  styleUrls: ['./mapa.component.scss']
})
export class MapaComponent implements AfterViewChecked {
  @ViewChild('mapContainer') mapContainer: ElementRef;
  @Input() mapToggle: boolean;
  @Output() makeMapToggle = new EventEmitter<boolean>();
  @Output() mapHeight = new EventEmitter<number>();
  @Output() searchRegion = new EventEmitter<string>();
  t = t[languageConfig];
  textColor = colors.black;
  groupColor = colors.white;
  clickedElement: string = '';

  constructor(private cdr: ChangeDetectorRef) { }

  ngAfterViewChecked() {
    this.mapHeight.emit(this.mapContainer.nativeElement.offsetHeight);
    this.cdr.detectChanges();
  }

  hasBeenClicked(id: string) {
    this.makeMapToggle.emit(false);
    if (id === this.clickedElement) {
      (document.getElementById(id).childNodes[0] as HTMLElement).style.fill = colors.white; // path
      (document.getElementById(id).childNodes[1] as HTMLElement).style.fill = colors.black;
      this.clickedElement = '';
      this.searchRegion.emit(SearchEnums.default);
      return;
    }
    this.searchRegion.emit(id);
    if (!!this.clickedElement) {
      (document.getElementById(this.clickedElement)
        .childNodes[0] as HTMLElement)
        .style.fill = colors.white;
      (document.getElementById(this.clickedElement)
        .childNodes[1] as HTMLElement)
        .style.fill = colors.black;
    }
    (document.getElementById(id).childNodes[0] as HTMLElement).style.fill = colors.selectedColor; // path
    (document.getElementById(id).childNodes[1] as HTMLElement).style.fill = colors.white;
    this.clickedElement = id;
  }

  changeFillColor(id: string) {
    const elem = document.getElementById(id);
    (elem.childNodes[0] as HTMLElement).style.fill = colors.hoverColor; // path
  }

  removeFillColor(id: string) {
    const elem = document.getElementById(id);
    (elem.childNodes[0] as HTMLElement).style.fill = this.clickedElement === id
      ? colors.selectedColor
      : colors.white; // path
    (elem.childNodes[1] as HTMLElement).style.fill = this.clickedElement === id
      ? colors.white
      : colors.black;
  }

  deleteMap() {
    if (!this.clickedElement) {
      return;
    }
    const elem = document.getElementById(this.clickedElement);
    if (!elem) {
      return;
    }
    (elem.childNodes[0] as HTMLElement).style.fill = colors.white;
    (elem.childNodes[1] as HTMLElement).style.fill = colors.black;
    this.clickedElement = '';
  }

  toggleMap() {
    this.makeMapToggle.emit(!this.mapToggle);
  }
}
